<template>
  <div id="blog_detail">
    <h3>{{blog.title|touppercase}}</h3>
    <article>{{blog.content|snnipe}}</article>


    <p>{{getCatagary}}</p>
    <p>{{blog.author}}</p>
  </div>
</template>

<script>
  export default {
    name: 'blog_detail',
    data() {
      return {
        id: this.$route.params.id,
        blog: {}
      }
    },
    created: function () {
      console.log("id" + this.id);
      this.$http.get("https://wd1872965327tmaost.wilddogio.com/post/" + this.id+".json").then(function (response) {
        console.log(response)
        this.blog = response.body;
        console.log(this.blog)
      })
    },
    computed:{
      getCatagary:function () {
        var s="";
        for(var position in this.blog.categaries){
          s+=this.blog.categaries[position]+" | ";
        }
        return s.slice(0,s.lastIndexOf("|"));
      }
    }

  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #blog_detail{
    max-width: 960px;
    margin: 20px auto;
    padding: 20px;
    background: #eee;
    border: 1px dotted #aaa;
  }
  li{
    display: inline;
    list-style-type: decimal;
    margin-left: 0px;
  }
  ul{
    display: inline-block;
    margin-right: 0;

  }
</style>



