<template>
    <nav id="blog_header">
      <ul>
        <li>
          <router-link to="/" exact></router-link>
        </li>
        <li>
        <router-link to="/add" exact></router-link>
        </li>
      </ul>
    </nav>
</template>

<script>
  export default {
    name: 'blog_header',
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
ul{
  list-style-type: none;
  text-align: center;
  margin: 0;
}
  li{
    display: inline-block;
    margin: 0 10px;
  }
  a{
    color: #fff;
    text-decoration: none;
    padding: 12px;
    border-radius: 5px;
  }
  nav{
    background: crimson;
    padding: 30px 0;
  }
  .router-link-active{
    background: rgba(233,255,255,0.8);
    color:#444;
  }
</style>



