<template>
  <div id="blog_list">
    <h1></h1>
    <input type="text" v-model="search" placeholder="">
    <div class="blog" v-for="blog in searchBlogs">
      <router-link v-bind:to="'/blog/'+blog.id">
        <h4 v-rainbow>{{blog.title|touppercase}}</h4>
        <article>{{blog.content|snnipe}}</article>
      </router-link>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'blog_list',
    data() {
      return {
        blogs: [],
        search: ""
      }
    },
    created: function () {
      this.$http.get("https://wd1872965327tmaost.wilddogio.com/post.json")
        .then(function (data) {
          console.log(data);
          return data.body;
        }).then(function (data) {
        console.log("----------------");
        console.log(data);
        var blogArray = [];
        for (var key in data) {
          data[key].id = key;
          blogArray.push(data[key])
          console.log(data[key]);
        }
        this.blogs = blogArray;
        console.log(this.blogs);
      })
    },
    computed: {
      searchBlogs: function () {
        return this.blogs.filter((blog) => {
          return blog.title.match(this.search);
        })
      }
    },
    // filters: {
    //   "to-uppercase": function (value) {
    //     return value.toUpperCase();
    //   }
    // return value.toUpperCase()
    // }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #blog_list {
    max-width: 800px;
    margin: 0 auto;
  }

  .blog {
    padding: 20px;
    margin: 20px auto;
    box-sizing: border-box;
    background: #eee;
  }

  a {
    text-decoration: none;
  }

  article {
    color: dimgrey;
  }

  input {
    width: 300px;
  }

</style>



