<template>
  <div id="blog_add">
    <h1></h1>
    <form v-on:submit="doAddBlog" v-if="!addSuccess">
      <label></label>
      <input v-model="blog.title" type="text" placeholder="" required>
      <br>

      <label></label>
      <textarea v-model="blog.content" placeholder=""></textarea>

      <div id="checkboxes">
        <input id="check0" type="checkbox" value="Vue.js" v-model="blog.categaries">
        <label for="check0">Vue.js</label>
        <input id="check1" type="checkbox" value="Node.js" v-model="blog.categaries">
        <label for="check1">Node.js</label>
        <input id="check2" type="checkbox" value="React.js" v-model="blog.categaries">
        <label for="check2">React.js</label>
        <input id="check3" type="checkbox" value="Angular4" v-model="blog.categaries">
        <label for="check3">Angular4</label>
      </div>
      <label></label>
      <select v-model="blog.author">
        <option v-for="author in blog.authors">
          {{author}}
        </option>
      </select>
      <br>
      <input type="submit" class="btnAdd" value="">
    </form>
    <div id="addToast" v-if="addSuccess">
      
    </div>
    <!--<hr>-->
    <!--<label></label>-->
    <!--<div>{{blog.title}}</div>-->
    <!--<div>{{blog.content}}</div>-->
    <!--<div>-->
    <!---->
    <!--</div>-->
    <div id="preview">
      <h3></h3>
      <p>{{blog.title}}</p>
      <p></p>
      <p>{{blog.content}}</p>
      <p></p>
      <ui>
        <li v-for="categary in blog.categaries">
          {{categary}}
        </li>
      </ui>
      <p>{{blog.author}}</p>

    </div>

  </div>
</template>

<script>
  export default {
    name: 'blog_add',
    data() {
      return {
        blog: {
          title: "",
          content: "",
          categaries: [],
          author: "",
          authors: [
            "",
            "",
            ""
          ]
        },
        addSuccess:false
      }
    },
    methods: {
      doAddBlog: function (e) {
        //
        this.$http.post("https://wd1872965327tmaost.wilddogio.com/post.json",this.blog).then(function (data) {
          // console.log(data);
            this.addSuccess=data.ok;
        })
        e.preventDefault()
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #blog_add * {
    box-sizing: border-box;
  }

  #blog_add{
    margin: 20px auto;
    max-width: 800px;
    /*padding: 10px;*/
  }
  label{
    display: block;
    margin: 20px 0;
  }
  input[type="text"],textarea,select{
    display: block;
    width: 100%;
    padding: 8px;
  }
  #checkboxes label{
    display: inline;
    margin-top: 0;
    margin-right: 15px;
  }
  #addToast{
    margin: 5px;
  }
  #checkboxes input{
    display: inline-block;
  }
  textarea{
    height: 150px;
  }
  input[type="submit"]{
    display: block;
    margin: 20px 0;
    background: crimson;
    color: white;
    border: 0;
    padding: 14px;
    border-radius: 5px;
    font-size: 18px;
    cursor: pointer;
  }
#preview{
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}
h3{
  margin-top: 10px;
}
</style>



